<template>
  <div id="app">
    <div class="container">
        <h1>ML application</h1>    
        <h3>Upload a image you want to classify</h3>
        <input type="file" @change="previewImage " accept="image/*">
        <div class="image-preview" v-if="selectedFile.length > 0">
            <img class="preview" :src="selectedFile">
        </div>
        <button @click="onUpload">Upload!</button>
        <button @click="corstest">Upload!</button>
        <h3>{{confidence}}</h3>

    </div>
</div>
</template>

<script>

export default {
  name: 'app',
  data: function () {
    return {
      confidence: 'P(x) =  ?',
      selectedFile : '',
    }
  },
    methods: {
    onUpload() {
        this.axios.post('http://172.16.98.152:5000/api/upload/', this.selectedFile)
        this.confidence = 'P(x)=0.92'
    },
    previewImage: function(event) {
            var input = event.target;
            if (input.files && input.files[0]) {
                var reader = new FileReader(); // for reading a image and covert to base64
                reader.onload = (e) => { this.selectedFile = e.target.result; }
                reader.readAsDataURL(input.files[0]); // base64 format
            }
    },
    corstest() {
        this.console.log("hoge")
        this.axios.post('http://172.16.98.152:5000/api/corstest/', {hoge : 1})
        .then( res => {
            this.console.log("hoge")
            this.console.log(res.data)
        })
    }
    }
}
</script>

<style>
* {
    box-sizing: border-box;
}
body {
    margin : 0px;
    padding:0;
    background-color : #aaa;
}
.container {
    height : 90vh;
    background-color : #fff;
    box-shadow: 0 0 20px #222;
    margin : 50px 100px 50px 100px;
    padding : 10px;
    text-align : center;
}
img.preview {
    margin:5px;
    width: 200px;
    background-color: white;
    border: 1px solid #DDD;
    padding: 5px;
}
</style>



